import React, { useState, useEffect } from 'react'
import { Button, Layout, Alert, Spin } from 'antd'
import styled from 'styled-components'
import { useUser } from '../../hooks/useUser'
import configs from '../../configs/configs'
import { useErrorMessage } from '../../hooks/useErrorMessage'
import { setHeader } from '../../libs/axios'
import { useLogin } from '../../api/auth'

const StyledAlert = styled(Alert)`
  text-align: center;
  margin-top: 20px;
`

const StyledLoginLayout = styled(Layout)`
  background-color: white;
  padding: 5%;
  margin: 4% 20%;
`
const StyledLoginHeader = styled.h1`
  font-size: 5vh;
`

const LoginPage = () => {
  const { login } = useUser()
  const { errMsg, clearErrMsg } = useErrorMessage()
  const [forLocalDev, setForLocalDev] = useState<boolean>(false)
  const [isLoading, setIsLoading] = useState<boolean>(false)

  const params = new Proxy(new URLSearchParams(window.location.search), {
    get: (searchParams: any, prop: any) => searchParams.get(prop),
  })

  const code = params.code
  const redirect = () => {
    window.location.replace(
      `https://login.microsoftonline.com/${configs.REACT_APP_TANENT_ID}/oauth2/v2.0/authorize?client_id=${configs.REACT_APP_CLIENT_ID}&response_type=code&redirect_uri=${configs.REACT_APP_REDIRECT_URL}&response_mode=query&scope=openid profile email Mail.Read offline_access&state=success`
    )
  }

  const useAuth = useLogin()

  const handleSetAuth = (code: string) => {
    if (code) {
      setIsLoading(true)
      useAuth.mutate(
        { code },
        {
          onSuccess: (data) => {
            setHeader(data.data.access_token.token)
            login(data.data.access_token?.token, data.data.refresh_token?.token)
            setIsLoading(false)
          },
          onError: () => {
            setIsLoading(false)
          }
        }
      )
    }
  }

  useEffect(() => {
    if (code) {
      if (window.location.origin === 'https://nlp-backoffice.web.app' || window.location.origin === "https://knlpweb-dev.kasikornbank.com") {
        const forLocalDev = confirm("For Dev Local ? ")
        forLocalDev ? setForLocalDev(true) : handleSetAuth(String(code))
      } else {
        handleSetAuth(String(code))
      }
    }
  }, [code])

  return (
    <Layout>
      <Spin spinning={isLoading}>
      <StyledLoginLayout>
        <StyledLoginHeader>
          KNLP Chatbot
        </StyledLoginHeader>
        {forLocalDev && code}
        {errMsg && (
          <StyledAlert
            message={errMsg}
            type="error"
            showIcon
            closable
            onClose={clearErrMsg}
          />
        )}
        <Button type="primary" onClick={redirect}>
          Sign in
        </Button>
      </StyledLoginLayout>
      </Spin>
    </Layout>
  )
}
export default LoginPage
